import axios from 'axios';
import qs from 'qs';

const defaultHeaders = {
  'Content-Type': 'application/json',
};

const isProd = process.env.NODE_ENV === 'production';

// eslint-disable-next-line no-unused-vars
const remote = 'http://139.155.127.14:9000';

export function getUrl() {
  const qsUrl = qs.parse(window.location.search.replace('?', ''));
  const defaultURL = isProd ? '' : 'http://localhost:9000';
  const baseURL = qsUrl.server_url ? qsUrl.server_url : defaultURL;
  return baseURL;
}

export const defaultMethods = ['GET', 'POST', 'OPTIONS', 'PUT', 'DELETE', 'PATCH', 'HEAD'];


export default function createAPI({
  baseURL = getUrl(),
  url,
  method,
  headers = defaultHeaders,
  body = {},
}) {
  const m = method ? method.toLowerCase() : 'get';
  const instance = axios.create({ baseURL, headers });
  if (m === 'get') {
    return instance.get(url);
  }
  return instance[m](url, body);
}
