<template>
  <el-dropdown v-show="servers"
               @command="select">
    <span class="server">
      {{server.url}}<i class="el-icon-arrow-up icon"></i>
    </span>
    <el-dropdown-menu slot="dropdown">
      <el-dropdown-item v-for="(server,type) in servers"
                        :command="server"
                        :key="type">
        {{server.name}}(<span class="url">{{server.url}}</span>)</el-dropdown-item>
    </el-dropdown-menu>
  </el-dropdown>
</template>

<script>
import { Dropdown, DropdownItem, DropdownMenu } from 'element-ui';
import { getUrl } from '../../api';

export default {
  components: {
    'el-dropdown': Dropdown,
    'el-dropdown-menu': DropdownMenu,
    'el-dropdown-item': DropdownItem,
  },
  methods: {
    select(server) {
      this.$store.commit('SET_SERVER', server);
    },
  },
  created() {
    this.$store.commit('SET_SERVER', {
      url: getUrl(),
      name: this.$t('default_server'),
    });
  },
  computed: {
    server() {
      return this.$store.state.server;
    },
    servers() {
      const servers = this.$store.state.baseInfo?.settings?.servers;
      if (servers) {
        return {
          ...servers,
          $_default_server: this.server,
        };
      }
      return null;
    },
  },
};
</script>

<style lang="scss" scoped>
.server {
  cursor: pointer;
}

.icon {
  margin-left: 10px;
}

.url {
  font-weight: 600;
}
</style>
