<template>
  <div class="locale-changer">
    <el-dropdown @command="select">
      <span class="current">{{current}}<i class="el-icon-arrow-down el-icon--right"></i></span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item v-for="(lang,key) in langs"
                          :key="`Lang${key}`"
                          :command="key">
          {{ lang }}
        </el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
  </div>
</template>

<script>
import { Dropdown, DropdownMenu, DropdownItem } from 'element-ui';

export default {
  name: 'i18n',
  components: {
    'el-dropdown': Dropdown,
    'el-dropdown-menu': DropdownMenu,
    'el-dropdown-item': DropdownItem,
  },
  data() {
    return {
      current: '',
    };
  },
  mounted() {
    this.current = this.langs[this.$i18n.locale];
  },
  computed: {
    langs() {
      return {
        en: 'English',
        zh: '中文',
      };
    },
  },
  methods: {
    select(type) {
      this.current = this.langs[type];
      this.$i18n.locale = type;
    },
  },
};
</script>
<style lang="scss" scoped>
.current {
  cursor: pointer;
}
</style>
