import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'index',
    component: () => import(/* webpackChunkName: "index" */'../pages/index.vue'),
  },
  {
    path: '/api',
    name: 'api',
    component: () => import(/* webpackChunkName: "api" */'../pages/api/index.vue'),
  },
  {
    path: '/api/info',
    name: 'api-info',
    component: () => import(/* webpackChunkName: "api-info" */'../pages/api-info/index.vue'),
  },
  {
    path: '/auth',
    name: 'auth',
    component: () => import(/* webpackChunkName: "auth" */'../pages/auth/index.vue'),
  },
  {
    path: '/doc',
    name: 'doc',
    component: () => import(/* webpackChunkName: "doc" */'../pages/doc/index.vue'),
  }];

const router = new VueRouter({
  mode: 'hash',
  base: '/',
  routes,
});

export default router;
