<template>
  <div class="tree">
    <div class="name"
         v-if="showName"
         :class="{active}"
         @click="clickDoc(doc)">{{doc.name}}</div>
    <div v-if="doc.children && doc.children.length > 0"
         v-show="show">
      <Tree v-for="child in doc.children"
            :key="getParent(child)"
            :doc="child"
            :parent="getParent(child)" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'Tree',
  props: {
    doc: Object,
    parent: String,
    showName: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      selected: false,
    };
  },
  computed: {
    menus() {
      return this.$store.state.menus;
    },
    active() {
      const isActive = (this.menus.join('/') === this.parent) && this.doc.filename !== '';
      if (isActive) {
        this.clickDoc(this.doc);
      }
      return isActive;
    },
  },
  methods: {
    show() {
      return false;
    },
    getParent(doc) {
      return `${this.parent}/${doc.name}`;
    },
    clickDoc(doc) {
      const query = { path: this.parent };
      if (doc.filetype === 'md') {
        if ('content' in doc) {
          this.$store.commit('SET_DOC', query.path);
          if (this.$route.query.path !== query.path) {
            this.$router.push({ name: 'doc', query });
          }
        }
      } else if ('body' in doc) {
        this.$store.commit('SET_API', query.path);
        if (this.$route.query.path !== query.path) {
          this.$router.push({ name: 'api', query });
        }
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.tree {
  margin: 5px 10px;
}

.name {
  cursor: pointer;
}

.active {
  color: #61affe;
}
</style>
