import Vue from 'vue';
import Vuex from 'vuex';
import { getApiDocs, getDocs } from '../api/docs';

Vue.use(Vuex);


async function sortDocs(doc) {
  const ref = doc;
  if (doc.filename) {
    const { data } = await getDocs(doc.filename);
    if (data.apis) {
      // 使用Vue.set使其变为可观察对象
      Vue.set(ref, 'children', data.apis);
    } else {
      Vue.set(ref, 'content', data.content);
    }
  }
  if (doc.children) {
    const children = Object.values(doc.children);
    if (children.length > 0) {
      ref.children = children.sort((a, b) => a.order - b.order);
      children.forEach(child => sortDocs(child));
    } else {
      ref.children = [];
    }
  }
}

function find(obj, path) {
  if (obj.children) {
    // eslint-disable-next-line no-restricted-syntax
    for (const item of obj.children) {
      const copPath = path.slice();
      if (item.name === copPath.shift()) {
        if (copPath.length === 0) {
          return item;
        }
        return find(item, copPath);
      }
    }
    return null;
  }
  return null;
}

export default new Vuex.Store({
  state: {
    baseInfo: null,
    server: null,
    auth: {},
    doc: { content: '' },
    api: {},
    apiInfo: {},
    menus: [],
  },
  mutations: {
    SET_BASE_INFO(state, info) {
      state.baseInfo = info;
      document.title = `${info.project_name} Panda api`;
    },
    SET_AUTH(state, auth) {
      state.auth = auth;
    },
    SET_API_INFO(state, apiInfo) {
      state.apiInfo = apiInfo;
    },
    SET_API(state, path) {
      state.api = find({ children: state.baseInfo.api_docs }, path.split('/'));
    },
    SET_DOC(state, path) {
      state.doc = find({ children: state.baseInfo.api_docs }, path.split('/'));
    },
    SET_MENUS(state, menus) {
      state.menus = menus;
    },
    SET_SERVER(state, server) {
      state.server = server;
    },
  },
  actions: {
    async getApiDocs({ commit }) {
      const res = await getApiDocs();
      const baseInfo = {
        ...res.data,
        api_docs: res.data.api_docs.sort((a, b) => a.order - b.order),
      };
      const finderSettings = baseInfo.api_docs.find(d => d.filetype === 'settings');
      const finderAuth = baseInfo.api_docs.find(d => d.filetype === 'auth');
      if (finderAuth) {
        const { data: auth } = await getDocs(finderAuth.filename);
        commit('SET_AUTH', auth);
      }
      if (finderSettings) {
        const { data: settings } = await getDocs(finderSettings.filename);
        baseInfo.api_docs = baseInfo.api_docs.filter(d => d.filetype !== 'settings');
        baseInfo.settings = settings;
      }
      await Promise.all(baseInfo.api_docs.map(doc => sortDocs(doc)));
      commit('SET_BASE_INFO', baseInfo);
    },
  },
});
