<template>
  <div class="menu"
       v-if="baseInfo"
       :style="{width: width+'px'}">
    <div class="title"
         @click="clickTitle">{{baseInfo.project_name}}</div>
    <div class="docs">
      <div class="doc"
           v-for="doc in baseInfo.api_docs"
           :key="doc.name">
        <div class="root"
             @click="setRootDoc(doc)">
          <i v-show="doc.filetype !== 'auth'"
             class="icon el-icon-arrow-right"
             :class="{active: isActive(doc) }" />
          <span class="name"
                :class="{active: isActive(doc)}">{{doc.name}}</span>
        </div>
        <Tree v-if="doc.children.length > 0 && isActive(doc)"
              :doc="doc"
              :parent="doc.name"
              :showName="false" />
      </div>
    </div>
    <div class="server">
      <Server />
    </div>
  </div>
</template>

<script>
import Tree from './Tree.vue';
import Server from './Server.vue';

export default {
  components: {
    Tree,
    Server,
  },
  props: {
    width: {
      type: Number,
      default: 250,
    },
  },
  watch: {
    '$route.query': {
      immediate: true,
      async handler({ path }) {
        if (this.baseInfo === null) {
          await this.$store.dispatch('getApiDocs');
        }
        if (path) {
          const paths = path.split('/');
          const root = paths[0];

          if (root) {
            const docFinder = this.baseInfo.api_docs.find(doc => doc.name === root);
            if (docFinder) {
              if (paths.length === 1) {
                this.setRootDoc(docFinder, false);
              }
              this.$store.commit('SET_MENUS', path.split('/'));
            }
          }
        }
      },
    },
  },
  computed: {
    baseInfo() {
      return this.$store.state.baseInfo;
    },
    menus() {
      return this.$store.state.menus;
    },
  },
  data() {
    return {
      loading: false,
    };
  },
  methods: {
    isActive(doc) {
      return this.menus && this.menus[0] === doc.name;
    },
    clickTitle() {
      this.to('index');
      this.$store.commit('SET_MENUS', []);
    },
    to(name) {
      if (this.$route.name !== name) {
        this.$router.push({ name });
      }
    },
    setRootDoc(doc, route = true) {
      const query = { path: doc.name };
      this.$store.commit('SET_MENUS', [doc.name]);
      if (doc.filetype === 'md') {
        if ('content' in doc) {
          this.$store.commit('SET_DOC', query.path);
          if (route && this.$route.query.path !== query.path) {
            this.$router.push({ name: 'doc', query });
          }
        }
      } else if (doc.filetype === 'json5') {
        this.$store.commit('SET_API_INFO', doc);
        if (route && this.$route.query.path !== query.path) {
          this.$router.push({ name: 'api-info', query });
        }
      } else if (doc.filetype === 'auth') {
        this.to('auth');
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.menu {
  position: fixed;
  box-sizing: border-box;
  height: 100vh;
  z-index: 1000;
  box-shadow: 2px 0 5px #ddd;
  background-color: white;
  overflow: auto;

  .title {
    padding: 20px 10px;
    font-size: 1.2rem;
    font-weight: 600;
  }

  .docs {
    margin: 10px 0;

    .doc {
      padding: 10px;

      .root {
        cursor: pointer;

        .icon {
          margin-right: 5px;
          transition: 0.2s;

          &.active {
            transform: rotate(90deg);
          }
        }
        .name {
          color: #333;
          font-weight: 600;
          transition: 0.2s;

          &.active,
          &:hover {
            color: #61affe;
          }
        }
      }
    }
  }

  .server{
    position: absolute;
    left: 10px;
    bottom: 10px;
    width: 100%;
    z-index: 1000;
  }
}
</style>
